import React from 'react';

import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import TopFollowers from './Followers/TopFollowers';
import FollowerImg from '../../../../assets/avatars/avatar_2.png';
import TopFollowings from './Followings/TopFollowings';
import TopPosts from './Posts/TopPosts';
import './Tabs.css';

function TabPanel({
  children, value, index, ...other
}) {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}
TabPanel.propTypes = {
  value: PropTypes.any.isRequired,
  children: PropTypes.node,
  index: PropTypes.any.isRequired
};
function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`
  };
}
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
    position: 'absolute',
    top: '0px',
    left: '0px',
    margin: '20px auto',
  }
}));

function FullWidthTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default" className="AppBar">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="FOLLOWERS" {...a11yProps(0)} className="TabsDecoration" />
          <Tab
            label="FOLLOWINGS"
            {...a11yProps(1)}
            className="TabsDecoration"
          />
          <Tab label="POSTS" {...a11yProps(2)} className="TabsDecoration" />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <TopFollowers ImgSrc={FollowerImg} />
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <TopFollowings ImgSrc={FollowerImg} />
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
          <TopPosts ImgSrc={FollowerImg} />
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}

export default FullWidthTabs;
